.calculator__item {
    margin-bottom: 2.2em;
    width: 100%;
}

.gr-calc input[type=range] {
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    cursor: pointer;
    width: 100%;
    height: 3px;
    border-radius: 10px;
    background: var(--slider-color);
    outline: none;
}

.gr-calc input[type=range]:focus {
    outline: none;
}

.gr-calc input[type=range]::-ms-track {
    width: 100%;
    cursor: pointer;
}

.gr-calc input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    border: 2.5px solid var(--vigo-color);
    height: 20px;
    width: 20px;
    border-radius: 40px;
    background: white;
    cursor: pointer;
    margin-top: 0px;
}

.gr-calc input[type=range]::-moz-range-thumb {
    border: 2.5px solid var(--vigo-color);
    height: 20px;
    width: 20px;
    border-radius: 40px;
    background: white;
    cursor: pointer;
}

.gr-calc input[type=range]::-ms-thumb {
    border: 2.5px solid var(--vigo-color);
    height: 20px;
    width: 20px;
    border-radius: 40px;
    background: white;
    cursor: pointer;
}

.gr-calc input[type=number] {
    -moz-appearance: none;
    -webkit-appearance: none;
    cursor: pointer;
    background: var(--input-number-color);
    border-style: none;
    height: 30px;
    margin-left: 20px;
    margin-right: 10px;
    text-align: right;
    padding-right: 5px;
    font-family: 'Roboto', sans-serif;
    font-size: 1.5em;
    border-radius: 4px;
    /* float: right; */
}

.gr-calc input[type=number]:focus {
    outline: none;
    border: 1px solid #3197EE;
}

.final-calculation-output p {
    margin-top: 1em;
    font-size: 1.2em;
    background: #f1f2f3;
    border-radius: 7px;
    padding: 20px;
}

